<template>
    <div class="bar_bda_con">
        <div ref="bar_bda" class="bar_bda"></div>
    </div>
</template>

<script>
  // import Interface from '@/base/mixin/channel-preference';
  // import {CP_PATH} from '@/config'

  let option = {
    tooltip: {
      trigger: 'axis',
      axisPointer: {            // 坐标轴指示器，坐标轴触发有效
        type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
      }
    },
    legend: {
      show:true,
      data: ['总调用数', '有效数'],
      padding:[0,0,0,0]
    },
    grid: {
      top:'30',
      left: '3%',
      right: '4%',
      bottom: '5%',
      containLabel: true
    },
    xAxis: {
      name: ' ',
      type: 'value',
      axisTick: {
        show: true  //坐标轴刻度线
      },
      axisLine: { //轴线
        show: false
      },
      splitLine: { //网格线
        show: true
      },
      axisLabel: { //坐标轴样式
        textStyle: {
          color: '#636363'
        }
      }
    },
    yAxis: {
      type: 'category',
      axisLine: {
        show: false
      },
      axisTick: {
        show: false
      },
      axisLabel: {
        textStyle: {
          color: '#636363'
        }
      },
      data: ['查询欠费', '查询退补电费', '查询工单进度']
    },
    series: [
      {
        barMaxWidth: 30,//柱图宽度
        color: '#23aed9',
        name: '总调用数',
        type: 'bar',
        stack: '总量',
        label: {
          show: true,
          position: 'insideRight'
        },
        showBackground: true,
        backgroundStyle: {
          color: '#e7fcff'
        },
        data: [320, 302, 301]
      },
      {
        barMaxWidth: 30,//柱图宽度
        color: '#fdc371',
        name: '有效数',
        type: 'bar',
        stack: '总量',
        label: {
          show: true,
          position: 'insideRight'
        },
        showBackground: true,
        backgroundStyle: {
          color: '#e7fcff'
        },
        data: [120, 132, 101],
        itemStyle: {
          normal: {
            label: {
              show: true, //开启显示
              position: 'right', //在上方显示
              textStyle: { //数值样式
                color: '#FF8000',
                fontSize: 14
              }
            }
          }
        }
      },
    ]
  };
  export default {
    name: "bar_bda",
    // mixins: [Interface],
    components: {
      // barTable,
    },
    props: {
      typeVal:{
        type:Array,
        default:function () {
          return []
        }
      }
    },
    data() {
      return {
        barOption: option,
      }
    },
    mounted() {
      this.init()

    },
    methods: {
        init(yAxisDtata=[],series0 = [0,0,0],series1=[0,0,0]) {
        this.barOption.yAxis.data = yAxisDtata
        this.barOption.series[0].data = series0
        this.barOption.series[1].data = series1
        let echarts = require('echarts');
        this.mychart = echarts.init(this.$refs.bar_bda)
        this.mychart.setOption(this.barOption);
        let mychart = echarts.init(this.$refs.bar_bda, true)  //监听窗口变化重新加载
        window.addEventListener("resize", mychart.resize); //监听窗口变化重新加载

        //点击事件
        // let _this = this
        // this.mychart.off('click');
        // this.mychart.on('click', function (params) {
        //  window.console.log(params)
        // })
      },
    }
  }
</script>

<style scoped lang="scss">
    .bar_bda_con {
        height: 100%;
        .bar_bda {
            height: 100%;
            min-height: 100px;
        }
    }
</style>