        <!--                @changeDetailPage="changePage"-->
        <!--                @exportExcel="themeLabelExportExcel"-->
        <!--            ></themeLabelList>-->
        <!--          </div>-->
        <!--        </div>-->
        <!--        <div class="content-right content-border">-->
        <!--          <div class="content-title">-->
        <!--            <span class="block"></span>-->
        <!--            <span>主题推荐关联关系</span>-->
        <!--          </div>-->
        <!--          <div class="chart-main">-->
        <!--            <bdaTable-->
        <!--                :tableData="recommendData"-->
        <!--                :headerColor="'#1899fa'"-->
        <!--                :indexWidth="0"-->
        <!--                :isPage="true"-->
        <!--                :pageConfig="recommendPageConfig"-->
        <!--                @changePage="changePage"-->
        <!--            ></bdaTable>-->
        <!--          </div>-->
        <!--        </div>-->
        <div class="content-left content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>智能IVR服务类型占比分布</span>
            </div>
          </div>
          <div class="chart-main">
            <pieCharts
              v-if="serviceTypeRes"
              :pieChartsData="serviceTypeData"
              :roseType="false"
              :labelDw="'%'"
              :radius="['30%', '70%']"
              :name="'IVR服务类型占比分布'"
              @setOption="setOption"
            ></pieCharts>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>热词分布</span>
            </div>
          </div>
          <div class="chart-main" id="randomCircle">
            <wordCloud
              v-if="hotWordRes"
              :wordCloudData="hotWordData"
              :name="'热词分布'"
              @setOption="setOption"
            ></wordCloud>
          </div>
        </div>
      </div>
      <div class="content" style="margin-top: 20px">
        <div class="content-left content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>意图推荐调用统计分布</span>
            </div>
            <div class="content-title-right">
              <el-date-picker
                v-model="dytjfbDate"
                type="date"
                size="mini"
                value-format="yyyy-MM-dd"
                placeholder="选择时间"
                @change="GetIntentionInvokeStatistics"
              ></el-date-picker>
            </div>
          </div>
          <div class="chart-main">
            <atarck-bar v-loading="dytjfbLoading" ref="atarckBar"></atarck-bar>
            <!--            <themeLabelList-->
            <!--                :labelData="labelData"-->
            <!--                :pageConfig="labelPageConfig"-->
            <!--                @changePage="changePage"-->
            <!--                :showDetailData="labelDetailData"-->
            <!--                @clickDetail="getLabelDetailData"-->
            <!--                @changeDetailPage="changePage"-->
            <!--                @exportExcel="themeLabelExportExcel"-->
            <!--            ></themeLabelList>-->
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>意图推荐调用次数统计分布</span>
            </div>
            <div class="content-title-right">
              <el-date-picker
                v-model="intentionRecommendTime"
                type="date"
                size="mini"
                value-format="yyyy-MM-dd"
                placeholder="选择时间"
                @change="getIntentionRecommendData"
              ></el-date-picker>
            </div>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="intentionRecommendRes"
              :barChartsData="intentionRecommendData"
              :barWidth="40"
              :barType="'bar'"
              :isShadow="true"
              :name="'意图推荐调用次数统计分布'"
              @setOption="setOption"
              :maxShowNumber="13"
            ></barCharts>
          </div>
        </div>
      </div>
    </div>
    <div class="center">
      <div class="content">
        <div class="content-bottom content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>智能IVR总体情况分布图</span>
            </div>
            <div class="content-title-right">
              <el-date-picker
                v-model="generalSituationTime"
                type="month"
                size="mini"
                value-format="yyyy-MM"
                placeholder="选择时间"
                @change="getGeneralSituationData"
              ></el-date-picker>
            </div>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="generalSituationRes"
              :barChartsData="generalSituationData"
              :legendStatus="true"
              :isMultiple="true"
              :isShadow="true"
              :barWidth="20"
              :rotateNum="0"
              :barType="'bar'"
              :name="'智能IVR总体情况分布图'"
              @setOption="setOption"
              :maxShowNumber="13"
            ></barCharts>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom" v-if="false">
      <div class="content">
        <div class="content-top content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>智能IVR用户流失率</span>
            </div>
            <div class="content-title-right">
              <el-date-picker
                v-model="userChurnTime"
                type="month"
                size="mini"
                value-format="yyyy-MM"
                placeholder="选择时间"
                @change="getUserChurnData"
              ></el-date-picker>
            </div>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="userChurnRes"
              :barChartsData="userChurnData"
              :barWidth="40"
              :barType="'bar'"
              :isShadow="true"
              :name="'智能IVR用户流失率'"
              @setOption="setOption"
              :maxShowNumber="13"
            ></barCharts>
          </div>
        </div>
        <div class="content-bottom content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>意图推荐调用次数统计分布</span>
            </div>
            <div class="content-title-right">
              <el-date-picker
                v-model="intentionRecommendTime"
                type="date"
                size="mini"
                value-format="yyyy-MM-dd"
                placeholder="选择时间"
                @change="getIntentionRecommendData"
              ></el-date-picker>
            </div>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="intentionRecommendRes"
              :barChartsData="intentionRecommendData"
              :barWidth="40"
              :barType="'bar'"
              :isShadow="true"
              :name="'意图推荐调用次数统计分布'"
              @setOption="setOption"
              :maxShowNumber="13"
            ></barCharts>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from "@/base/mixin/user-behavior";
import barCharts from "@/components/bar-charts/bar-charts";
import pieCharts from "@/components/pie-charts/pie-charts";
import wordCloud from "@/components/word-cloud/word-cloud";
// import bdaTable from '@/components/bda-table/bda-table'
import atarckBar from "./components/atarck-bar";
// import themeLabelList from '@/components/theme-label-list/theme-label-list'
import { exportExcelGet } from "@/utils/export";

export default {
  components: {
    barCharts,
    pieCharts,
    wordCloud,
    // bdaTable,
    atarckBar
    // themeLabelList
  },
  name: "panoramicAnalysis",
  mixins: [Interface],
  data() {
    return {
      //关注标签
      labelData: [],

      labelQueryForm: {
        menuType: "1",
        current: 1,
        size: 10
      },
      //分页设置
      labelPageConfig: {
        name: "label",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //标签详情
      labelDetailData: {},
      labelDetailQueryForm: {
        dbcolumnAS: "",
        menuType: "1",
        tagName: "",
        current: 1,
        size: 10
      },
      //分页设置
      labelDetailPageConfig: {
        name: "labelDetail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //策略推荐时间
      strategyTime: [],
      // 主题推荐关联关系
      recommendData: {},
      recommendRes: false,
      recommendForm: {
        menuType: "1",
        current: 1,
        size: 10
      },
      //分页设置
      recommendPageConfig: {
        name: "recommend",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //IVR服务类型占比分布
      serviceTypeData: {},
      serviceTypeRes: false,
      //热词分布
      hotWordData: {},
      hotWordRes: false,
      //智能IVR总体情况分布
      generalSituationTime: this.getDate("month"),
      generalSituationData: {},
      generalSituationRes: false,
      //智能IVR用户流失率
      userChurnTime: this.getDate("month"),
      userChurnData: [],
      userChurnRes: false,
      //意图推荐调用次数统计分布
      intentionRecommendTime: this.getDate("date"),
      intentionRecommendData: [],
      intentionRecommendRes: false,
      //图表组件数据
      setOptionData: {},
      //意图调用统计分布
      dytjfbDate: this.getDate("date"),
      dytjfbLoading: false
    };
  },
  mounted() {
    this.getLabelData();
    this.getRecommendData();
    this.getServiceTypeData();
    this.getHotWordData();
    this.getGeneralSituationData();
    // this.getUserChurnData()
    this.getIntentionRecommendData();
    this.GetIntentionInvokeStatistics();
    window.addEventListener("resize", () => {
      if (this.setOptionData["IVR服务类型占比分布"])
        this.setOptionData["IVR服务类型占比分布"].resize();
      if (this.setOptionData["热词分布"])
        this.setOptionData["热词分布"].resize();
      if (this.setOptionData["智能IVR总体情况分布图"])
        this.setOptionData["智能IVR总体情况分布图"].resize();
      if (this.setOptionData["智能IVR用户流失率"])
        this.setOptionData["智能IVR用户流失率"].resize();
      if (this.setOptionData["意图推荐调用次数统计分布"])
        this.setOptionData["意图推荐调用次数统计分布"].resize();
    });
  },
  methods: {
    //图表组件
    setOption(name, val) {
      this.setOptionData[name] = val;
    },
    //改变分页
    changePage(data) {
      console.log(data, "data");
      if (data.name === "label") {
        this.labelQueryForm.size = data.size;
        this.labelQueryForm.current = data.current;
        this.getLabelData();
      }
      if (data.name === "labelDetail") {
        this.labelDetailQueryForm.dbcolumnAS = data.keyValue || "";
        this.labelDetailQueryForm.size = data.size;
        this.labelDetailQueryForm.current = data.current;
        this.getLabelDetailData();
      }
      if (data.name === "recommend") {
        this.recommendForm.size = data.size;
        this.recommendForm.current = data.current;
        this.getRecommendData();
      }
    },

    //获取当前年月日
    getDate(type) {
      var nowDate = new Date();
      var year = nowDate.getFullYear();
      var month =
        nowDate.getMonth() + 1 < 10
          ? "0" + (nowDate.getMonth() + 1)
          : nowDate.getMonth() + 1;
      var day =
        nowDate.getDate() < 10 ? "0" + nowDate.getDate() : nowDate.getDate();
      if (type === "month") {
        return year + "-" + month;
      }
      if (type === "date") {
        return year + "-" + month + "-" + day;
      }
    },
    //主题标签导出
    themeLabelExportExcel(data) {
      console.log(data);
      exportExcelGet(
        "/leanSeatingSkillController/exportLabelUserDisplay" +
          "?tagId=" +
          data.tagId
      );
    },
    // 获取关注标签
    getLabelData() {
      this.themeLabelDisplay(this.labelQueryForm).then(res => {
        this.labelPageConfig.total = res.total;
        this.labelPageConfig.size = this.labelQueryForm.size;
        this.labelPageConfig.current = this.labelQueryForm.current;
        this.labelData = res.list.map(e => {
          return {
            ...e,
            name: e.tagName,
            text: e.tagDescription,
            value: e.account || 0
          };
        });

        console.log(this.labelData, "labelData");
      });
    },
    //获取标签详情信息
    getLabelDetailData(data) {
      if (data) {
        this.labelDetailQueryForm.tagId = data.tagId;
        this.labelDetailQueryForm.size = 10;
        this.labelDetailQueryForm.current = 1;
      }
      let columnNameData = [];
      this.labelUserDisplay({ ...this.labelDetailQueryForm }).then(res => {
        this.labelDetailPageConfig.total = res.result[0]
          ? res.result[0].total
          : 0;
        this.labelDetailPageConfig.size = this.labelDetailQueryForm.size;
        this.labelDetailPageConfig.current = this.labelDetailQueryForm.current;
        if (res.showField[this.labelDetailQueryForm.tagId]) {
          columnNameData = res.showField[this.labelDetailQueryForm.tagId]
            .map(e => {
              return {
                prop: e.dbcolumnAS,
                label: e.dbcolumnTitle
              };
            })
            .filter(
              e =>
                e.label !== "地区编码" &&
                e.label !== "县区局编码" &&
                e.label !== "供电单位编码" &&
                e.label !== "供电电网编码"
            );
        }
        console.log(columnNameData, "columnNameData");
        res.result.shift();
        this.labelDetailData = {
          tableData: [...res.result],
          columnNameData: columnNameData,
          name: "labelDetail",
          headerColor: "#1899fa",
          title: "客户明细列表",
          total: this.labelDetailPageConfig.total,
          current: this.labelDetailQueryForm.current,
          size: this.labelDetailQueryForm.size
        };
        // console.log(this.labelDetailData, 'labelDetailData')
      });
    },
    // 获取主题推荐关联关系
    getRecommendData() {
      let columnNameData = [
        { prop: "relationNumber", label: "关系编号" },
        { prop: "relationName", label: "关联关系名称" },
        { prop: "descibe", label: "关联关系描述" }
      ];
      this.topicRecAssociation(this.recommendForm)
        .then(res => {
          console.log(res, "topicsRecommendAssociations");
          this.recommendPageConfig.total = res.total;
          this.recommendPageConfig.size = this.recommendForm.size;
          this.recommendPageConfig.current = this.recommendForm.current;
          this.recommendData = {
            tableData: [...res.list],
            columnNameData: columnNameData
          };
        })
        .catch(err => {
          if (err) {
            this.recommendData = {
              tableData: [],
              columnNameData: columnNameData
            };
          }
        });
    },
    // 获取IVR服务类型占比分布
    getServiceTypeData() {
      this.serviceTypeRes = false;
      this.proportionDistributionServiceTypes().then(res => {
        this.serviceTypeData = {
          dataMap: res.map(e => {
            return {
              name: e.ivrfwlx,
              value: e.zb
            };
          }),
          colorMap: ["#1A84F4", "#08BEF3", "#1DE0BF", "#EAA01A"]
        };
        this.serviceTypeRes = true;
      });
    },
    sortBy: function(attr, rev) {
      //第二个参数没有传递 默认升序排列
      if (rev == undefined) {
        rev = 1;
      } else {
        rev = rev ? 1 : -1;
      }

      return function(a, b) {
        a = a[attr];
        b = b[attr];
        if (a < b) {
          return rev * -1;
        }
        if (a > b) {
          return rev * 1;
        }
        return 0;
      };
    },
    //获取热词
    getHotWordData() {
      this.hotWordRes = false;
      this.hotWordDistribution().then(res => {
        console.log(res, "hotWordDistribution");
        if (res[1]) {
          let sortArr = res[1].sort(this.sortBy("rcsl", true));
          this.hotWordData = sortArr.map((e, index) => {
            e.symbolSize = 80 + index * 5;
            return {
              name: e.rc,
              valueShow: e.rcsl,
              symbolSize: e.symbolSize
            };
          });
          this.hotWordRes = true;
          console.log(this.hotWordData, "hotWordData");
        }
      });
    },
    //智能IVR总体情况分布
    getGeneralSituationData() {
      this.generalSituationRes = false;

      this.generalSituation({ selectTime: this.generalSituationTime }).then(
        res => {
          // res = "{\"code\":\"00000\",\"data\":{\"znivrl\":[{\"count\":75231,\"time\":\"202001\",\"percent\":null},{\"count\":63041,\"time\":\"202002\",\"percent\":null},{\"count\":39162,\"time\":\"202003\",\"percent\":null},{\"count\":87735,\"time\":\"202004\",\"percent\":null},{\"count\":57336,\"time\":\"202005\",\"percent\":null},{\"count\":60124,\"time\":\"202006\",\"percent\":null},{\"count\":66835,\"time\":\"202007\",\"percent\":null},{\"count\":33094,\"time\":\"202008\",\"percent\":null},{\"count\":79801,\"time\":\"202009\",\"percent\":null},{\"count\":65433,\"time\":\"202010\",\"percent\":null},{\"count\":27900,\"time\":\"202011\",\"percent\":null},{\"count\":77900,\"time\":\"202012\",\"percent\":null}],\"yhs\":[{\"count\":3000,\"time\":\"202001\",\"percent\":null},{\"count\":3557,\"time\":\"202002\",\"percent\":null},{\"count\":2344,\"time\":\"202003\",\"percent\":null},{\"count\":5678,\"time\":\"202004\",\"percent\":null},{\"count\":5423,\"time\":\"202005\",\"percent\":null},{\"count\":5478,\"time\":\"202006\",\"percent\":null},{\"count\":5487,\"time\":\"202007\",\"percent\":null},{\"count\":8357,\"time\":\"202008\",\"percent\":null},{\"count\":5487,\"time\":\"202009\",\"percent\":null},{\"count\":6542,\"time\":\"202010\",\"percent\":null},{\"count\":3547,\"time\":\"202011\",\"percent\":null},{\"count\":8767,\"time\":\"202012\",\"percent\":null}],\"hwl\":[{\"count\":163580,\"time\":\"202001\",\"percent\":null},{\"count\":134402,\"time\":\"202002\",\"percent\":null},{\"count\":149877,\"time\":\"202003\",\"percent\":null},{\"count\":190521,\"time\":\"202004\",\"percent\":null},{\"count\":129637,\"time\":\"202005\",\"percent\":null},{\"count\":133800,\"time\":\"202006\",\"percent\":null},{\"count\":149674,\"time\":\"202007\",\"percent\":null},{\"count\":163051,\"time\":\"202008\",\"percent\":null},{\"count\":143395,\"time\":\"202009\",\"percent\":null},{\"count\":173622,\"time\":\"202010\",\"percent\":null},{\"count\":159986,\"time\":\"202011\",\"percent\":null},{\"count\":159986,\"time\":\"202012\",\"percent\":null}]},\"codeMsg\":\"请求成功\"}";
          // res = JSON.parse(res).data;
          console.log(res, "generalSituation");
          let dataMap1 = [];
          let dataMap2 = [];
          let dataMap3 = [];
          let xNameData = [];
          res["znivrl"].forEach(e => {
            dataMap1.push(e.count);
            xNameData.push(e.time);
          });
          res["hwl"].forEach(e => {
            dataMap2.push(e.count);
          });
          res["yhs"].forEach(e => {
            dataMap3.push(e.count);
          });
          this.generalSituationData = {
            dataMap: {
              话务量: dataMap2,
              智能IVR量: dataMap1,
              用户数: dataMap3
            },
            xNameData: xNameData,
            colorMap: {
              话务量: [
                // '#55BFFF', '#2790FF'
                ["#E69622", "#F8CC67", "#fdf3df"]
              ],
              智能IVR量: [
                // '#55BFFF', '#2790FF'
                ["#2790FF", "#55BFFF", "#ddf2ff"]
              ],

              用户数: [
                // '#55BFFF', '#2790FF'
                ["#19CDAE", "#77E7BE", "#dff9f1"]
              ]
            },
            xyName: {
              x轴: { name: "", color: "#808080", diffcolor: false },
              y轴: [{ name: "", color: "#808080" }]
            }
            // xyName: { 'x轴': { name: '', color: "#808080", diffcolor: false }, 'y轴': [{ name: '话务量', color: '#808080' }, { name: '智能IVR量及用户数', color: '#808080' }, { name: '', color: 'transparent' }] }
            // xyName: { 'x轴': { name: '', color: "#808080", diffcolor: false }, 'y轴': [{ name: '数量', color: '#808080' }] }
          };
          this.generalSituationRes = true;
        }
      );
    },
    // 智能IVR用户流失率
    getUserChurnData() {
      this.userChurnRes = false;
      this.userChurnRate({ selectTime: this.userChurnTime }).then(res => {
        let dataMap = [];
        let xNameData = [];
        res.forEach(e => {
          dataMap.push(e.count);
          xNameData.push(e.percent);
        });
        this.userChurnData = {
          dataMap: {
            智能IVR用户流失率: dataMap
          },
          xNameData: xNameData,
          colorMap: {
            智能IVR用户流失率: [["#28B16E", "#6AE3AA", "#dff8ec"]]
          },
          xyName: {
            x轴: { name: "区间", color: "#4D4D4D", diffcolor: false },
            y轴: [{ name: "调用次数", color: "#808080" }]
          }
        };
        this.userChurnRes = true;
      });
    },
    //意图推荐调用统计分布
    GetIntentionInvokeStatistics() {
      this.dytjfbLoading = true;
      this.getIntentionInvokeStatistics({ date: this.dytjfbDate }).then(res => {
        window.console.log(res);
        if (res.length < 1) {
          this.dytjfbLoading = false;
          return;
        }
        let yAxisDtata = [];
        let series0 = []; //总调用数
        let series1 = []; //有效数
        for (let key in res) {
          yAxisDtata.push(key);
          series0.push(res[key].total || 0);
          series1.push(res[key].effectiveCount || 0);
        }
        this.$refs.atarckBar.init(yAxisDtata, series0, series1);
        this.dytjfbLoading = false;
      });
    },
    // 意图推荐调用次数统计分布
    getIntentionRecommendData() {
      this.intentionRecommendRes = false;
      this.getIntentionInvokeCount({ date: this.intentionRecommendTime }).then(
        res => {
          let dataMap = [];
          let xNameData = [];
          res.forEach(e => {
            dataMap.push(e.total);
            xNameData.push(e.dysj.split("-")[1] + "-" + e.dysj.split("-")[2]);
          });
          this.intentionRecommendData = {
            dataMap: {
              意图推荐调用次数统计分布: dataMap
            },
            xNameData: xNameData,
            colorMap: {
              意图推荐调用次数统计分布: [["#19A7CD", "#66D6FF ", "#dff6fe"]]
            },
            xyName: {
              x轴: { name: "时间", color: "#4D4D4D", diffcolor: false },
              y轴: [{ name: "调用次数", color: "#808080" }]
            }
          };
          this.intentionRecommendRes = true;
        }
      );
    }
  }
};
</script>

